import React, {Fragment} from 'react';
import {connect} from "react-redux";
import {IconPack} from "../../../../Images/IconPack";

import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Button from "@material-ui/core/Button";
import {setLicenseDaysSettingsModal, setChangeDealModal} from "../../../../redux/app/app.actions";
import TextField from "@material-ui/core/TextField/TextField";

const DealsAccordion = (props) => {

    const [expanded, setExpanded] = React.useState(false);


    const buttonHandler = (wrapperClassName) => {

        document.getElementsByClassName(wrapperClassName)[0].classList.add('editState');
    }

    const getAccordions = () => {
        return props.deals.map((i, key) => {
            return getAccordionItem(i, key);
        });
    };

    const handleAccordionsChange = (panel) => (event, isExpanded) => {

        setExpanded(isExpanded ? panel : false);
    };

    const getAccordionContent = (item, key) => {
        return (
            <div className={'dealItem-' + key + ' dealItem'}>
                <main className={'editState'}>

                    <div className="alertPanel mb-10 mt-10">{IconPack.userIcon} Lionel Messi offers to join to Corporate
                        license
                    </div>


                    <TextField
                        variant="filled"
                        label="Buyer Login"
                        className="w100percent mb-10"
                        value={item.buyer}
                        disabled

                    />
                    <TextField
                        variant="filled"
                        label="Seller Login"
                        className="w100percent mb-10"
                        value={item.seller}
                        disabled
                    />


                    <TextField
                        variant="filled"
                        label="Amount to send"
                        className="w100percent mb-10"
                        value={item.amountToSend}
                        disabled
                    />
                    <TextField
                        variant="filled"
                        disabled
                        label="Comision rate"
                        className="w100percent mb-10"
                        value={'1%'}

                    />
                    <TextField
                        variant="filled"
                        disabled
                        label="Amount to recive"
                        className="w100percent mb-10"
                        value={item.amountToRecive}
                    />


                    <TextField
                        label="Deal title"
                        fullWidth
                        rows={1}
                        disabled
                        value={item.title}
                        variant="filled"
                        className="w100percent mb-10"
                    />
                    <TextField
                        label="Terms of transaction"
                        fullWidth
                        rows={11}
                        disabled
                        multiline
                        value={item.description}
                        className={'height-auto'}
                        variant="filled"
                        className="w100percent mb-10"
                    />


                    <div className="btn btn--blue" onClick={() => {
                    }}>Cancel a deal
                    </div>
                </main>

                <main className={'infoState'}><span className="accordion-data-description">Status</span>
                    {item.status}
                    <span className="accordion-data-description">Seller</span>
                    <span
                        className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}{item.seller}</span>

                    <span className="accordion-data-description">Buyer</span>
                    <span
                        className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}{item.buyer}</span>


                    <span className="accordion-data-description">Amount</span>
                    {item.amount}

                    <span className="accordion-data-description">Deal Id</span>
                    {item.dealId}

                    <span className="accordion-data-description">Date created</span>
                    {item.dateCreated}

                    <Button variant="contained" className="btn btn--blue mb-15 mt-10" onClick={() => {
                        buttonHandler('dealItem-' + key)
                    }}>
                        Details
                    </Button>
                </main>
            </div>
        );
    }


    const getAccordionItem = (item, key) => {
        return (
            <Accordion expanded={expanded === 'panel' + key} onChange={handleAccordionsChange('panel' + key)}>
                <AccordionSummary
                    aria-controls={"panel" + key + "a-content"}
                    id={"panel" + key + "a-header"}
                >
                    {item.title}
                </AccordionSummary>
                <AccordionDetails>

                    {getAccordionContent(item, key)}


                </AccordionDetails>
            </Accordion>
        )
    };


    return (<div className="сustomStyle">
        <div className='tabs_wrapper'>
            <span className="mb-5 tabsContentHeader">Deal title</span>
            {getAccordions()}
        </div>
    </div>);
}


const mapStateToProps = (state) => ({
    deals: state.deals.deals,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLicenseDaysSettingsModal: (type) => dispatch(setLicenseDaysSettingsModal(type)),
        setChangeDealModal: (type) => dispatch(setChangeDealModal(type)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DealsAccordion);