import React, {Fragment} from 'react';
import {connect} from "react-redux";
import { useHistory } from "react-router-dom";

import DateProvider from "../../../../DateProvider/DateProvider";



const MessageTable = (props) => {
    let history = useHistory();

    const rowHandler = (data) => {
        history.push('tickets/'+data.chatId);
    }


    const rows = props.tickets.map((item, key) => {
        return (
            <div className="accordion-link" onClick={()=>{rowHandler(item)}}>
                <span>{item.title} </span>
                <span><DateProvider onlyDate={true} timestamp={item.data[item.data.length-1].date}/></span>
            </div>
        )
    });



    return (
        <div className="сustomStyle">
            <span className="mb-5 tabsContentHeader">Tickets title</span>
           {rows}
        </div>
    );
}
const mapStateToProps = (state) => ({
    tickets: state.tickets.tickets,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MessageTable);