import React from "react";


const DateProvider = (props) => {

    let a = new Date(props.timestamp * 1000);
    let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    let year = a.getFullYear();

    let month = months[a.getMonth()];

    let monthNumb = a.getMonth();
    if(monthNumb < 10) {
        monthNumb = '0'+ monthNumb;
    }

    let date = a.getDate();
    if(date < 10) {
        date = '0'+ date;
    }

    let hour = a.getHours();
    if(hour < 10) {
        hour = '0'+ hour;
    }

    let min = a.getMinutes();
    if(min < 10) {
        min = '0' + min;
    }

    let sec = a.getSeconds();
    if(sec < 10) {
        sec = '0' + sec;
    }

    let time = date + ' ' + month + ' ' + ' ' + hour + ':' + min + ':' + sec;
    if(props.onlyDate ) {
         time = date + ' ' + month;
    }
    if(props.onlyTime){
        time =  hour + ':' + min + ':' + sec;
    }

    if(props.fullDate){
         time = date + '.' + monthNumb + '.' + year + ', ' + hour + ':' + min + ':' + sec;
    }

    return time;
}


export default DateProvider;
